a {
    text-decoration: none;
}

.navigation {
    max-width: 1140px;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    padding-top: 30px;
}

.link {
    background-color: #01bcd5;
    width: 50%;
    height: 50px;
    color: #fdfaf5;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.active {
    opacity: 0.6;
}

.descriptionTask {
    display: flex;
    max-width: 1140px;
    margin: 0 auto;
    color: #cec1b7;
    justify-content: space-around;
    padding: 20px 0;
    font-size: 15px;
    text-align: center;
}

.tasksInfo {
    padding: 0 0 50px 0;
}
