p {
    margin: 0;
    display: block;
    margin-block-start: 0;
    margin-block-end: 0;
}

.timer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.clock {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 50px;
    padding: 100px;
    border-radius: 50%;
    box-shadow: 0px 14px 38px -9px rgba(117,116,117,1);
    color: blue;
    font-size: 40px;
    gap: 5px;
    height: 80px;
    width: 80px;
}