.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modalContent {
    padding: 20px;
    background-color: #fff;
    width: 600px;
}

.modalTitle {
    color: #c13161;
    font-size: 22px;
    font-weight: bold;
    text-align: start;
    padding: 10px 0;
}

.modalText {
    color: #6d6d6d;
    font-size: 14px;
    padding: 10px 0 20px 0;
    text-align: start;
}

.closeModal {
    text-align: end;
}